.App {
  /* max-width: 900px; */
}

.App h1 {
  margin: 0px;
  color: white;
  font-size: 65px;
}

.App h2 {
  margin: 0px;
}

.resume-front {
  width: fit-content;
  height: auto;
  margin: 60px auto;
}

.fix-it {
  width: fit-content;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  align-items: center;
}

.author-wants {
  width: fit-content;
  height: fit-content;
  white-space: nowrap;
  transform: translateY(7px);
  margin: 0px 20px;
}

.author-wants h2 {
  font-size: 20px;
  font-style: italic;
}

.contact-me {
  width: fit-content;
  height: fit-content;
  margin: 20px auto 0px;
}

.contact-me img {
  cursor: pointer;
}

.contact-me img:active {
  opacity: 0.7;
}

.skill-me p {
  color: #3E3E3E;
  font-style: italic;
  font-size: 25px;
  text-align: center;
}

.skill-me p:nth-child(3) {
  text-decoration: line-through;
}

.social-media {
  width: fit-content;
  margin: 0px auto;
}

.social-media .-github {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  margin: 20px 0px;
}

.social-media .-github p {
  color: #08457C;
  font-weight: bold;
  text-decoration-line: underline;
  font-size: 20px;
  margin: auto 10px;
  cursor: pointer;
}

.social-media .-twitter {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  margin: 20px 0px;
}

.social-media .-twitter p {
  color: #08457C;
  font-weight: bold;
  text-decoration-line: underline;
  font-size: 20px;
  margin: auto 10px;
  cursor: pointer;
}

.own-me {
  text-align: center;
  margin: 100px 0px;
}

.own-me p {
  font-weight: bold;
  margin: 7px 0px;
}